/* eslint-disable camelcase */
import React, { forwardRef } from 'react';
import {
  Form,
  Input,
  Select,
  Collapse,
  Row,
  Col,
  Cascader,
  InputNumber,
  Radio,
  Divider,
  AutoComplete,
  Button,
  Checkbox,
  TreeSelect,
  ConfigProvider,
  DatePicker,
  TimePicker,
} from 'antd';
import { FormProps, FormInstance } from 'antd/es/form';
import CardPro from '../CardPro';
import UploadGrid from '../UploadGrid';
import InputCount from '../InputCount';
import zhCN from 'antd/es/locale/zh_CN';
import 'moment/locale/zh-cn';
import locale from 'antd/es/date-picker/locale/zh_CN';
import {
  LoadingOutlined,
  PlusOutlined,
  UploadOutlined,
  AudioOutlined,
} from '@ant-design/icons';
import {
  colLayout_one,
  colLayout_two,
  colLayout_third,
  colLayout_fourth,
  fromItemLayout_two_row,
  fromItemLayout_conspan_two,
  fromItemLayout_conspan_third,
  fromItemLayout_conspan_fourth,
  fromItemLayout_third_row,
  fromItemLayout_conspan_one,
  formDefaultFormItemLayout,
} from './formLayout';
import './index.css';

import { HideContext, useHideContext } from "./Hide/context"
import { GetStoreProps } from "./Hide/interface"
import useFormItemHide from "./Hide/store"
import Hide from "./Hide"

export { getChildItemFun as getChildFormItemFun } from "./utils"

const Panel = Collapse.Panel;
const FormItem = Form.Item;
const Option = Select.Option;
const { Search } = Input;
const { RangePicker, MonthPicker } = DatePicker;

declare type FormLayout = 'horizontal' | 'inline' | 'vertical';

interface ItemsProps<T> {
  defaultcolspan?: any;
  /** Ë°®ÂçïÂÖÉÁ¥†Ê†áÈ¢ò */
  label: string;
  /** Ë°®ÂçïÂêçÁß∞ antd from ÁªÑ‰ª∂ getFieldDecorator Á¨¨‰∏Ä‰∏™ÂèÇÊï∞ */
  name: string;
  /** Ë°®ÂçïÂàùÂßãÂÄº */
  initialValue?: string | any;
  /** Ë°®ÂçïÊòØÂê¶Áã¨Âç†‰∏ÄË°å  */
  full?: boolean;
  /** Ë°®ÂçïÈöêËóè  */
  hideInForm?: boolean;
  /** input select Á≠âË°®ÂçïÁªÑ‰ª∂Â±ûÊÄßÈõÜÂêà ÂÖ∑‰ΩìÂèÇËÄÉ antd  */
  attributes?: T | any;
  type?: string | undefined;
  options?: Array<{ label: string; value: string | number }> | [] | undefined;
  span?: number;
  render?: JSX.Element;
}

export interface QuickFormProps<Values> extends FormProps<Values> {
  /** Ë°®ÂçïÈõÜÂêà */
  formDatas: Array<ItemsProps<object>>;
  /** antd collapse ÁªÑ‰ª∂Â±ûÊÄßÈõÜÂêà */
  collapseAttributes?: Object;
  /** antd collapse.panel ÁªÑ‰ª∂Â±ûÊÄßÈõÜÂêà */
  panelAttributes?: Object;
  /** ÊäòÂè†Ë°®Âçï‰∏ãÊòØÂê¶ÂàùÂßãÂåñÈÄâ‰∏≠Èù¢Êùø */
  visible?: boolean;
  /** Ë°®ÂçïÂçïË°åÊéíÂàó */
  colspan?: number;
  /** ÁªÑ‰ª∂Â§¥ÈÉ®Ê†áÈ¢ò */
  header?: React.ReactNode | any;
  /** Ëá™ÂÆö‰πâË°®ÂçïÊ†ÖÊ†ºÂÆΩÂ∫¶Âç†ÊØî,ÂèÇÁÖß antd Ê†ÖÊ†ºÂ∏ÉÂ±Ä */
  defaultFormItemLayout?: Object;
  /** Ëá™ÂÆö‰πâË°®ÂçïÊéíÂàóÊñπÂºè */
  defaultFormLayout?: FormLayout;
  /** Â∞∫ÂØ∏,ÂèÇÁÖß antd */
  size?: any;
  /** Ë°®ÂçïÁ±ªÂûã:modal&cardform&CardPro */
  type?: string;
  /** antd collapse.panel Ëá™ÂÆö‰πâÊ∏≤ÊüìÊØè‰∏™Èù¢ÊùøÂè≥‰∏äËßíÁöÑÂÜÖÂÆπ */
  extra?: any;

  /** Form.useFormItemHide ËøîÂõûÂÄº  */
  formHide?: GetStoreProps;
  /** ÂàùÂßãÂÄº ÈöêËóèÊòæÁ§∫ Â≠óÊÆµÂØπÂ∫îÁöÑÂÄº */
  initialHide?: { [x: string]: boolean };
}

export type QuickFormComponent<Values = any> = (
  props: QuickFormProps<Values>,
  ref?:
    | ((instance: FormInstance<Values> | null) => void)
    | React.RefObject<FormInstance<Values>>
    | null
    | undefined,
) => React.ReactElement;

const QuickForm: QuickFormComponent = (props, ref) => {
  const {
    collapseAttributes,
    panelAttributes,
    visible = false,
    type = 'cardform',
    extra,
    formDatas,
    colspan = 3,
    header,
    defaultFormLayout = 'vertical',
    defaultFormItemLayout = formDefaultFormItemLayout,
    size = 'default',
    formHide,
    initialHide,
    ...otherProps
  } = props;

  const [hide] = useFormItemHide(formHide)
  hide.setInitialValues(initialHide || {}, true)

  const HideFormItemDoM = []; // ÈöêËóèÁöÑË°®Âçï
  const FormItemDoM = [];
  let rowcolspan: string | any; // col ÈáåÁöÑÂ∏ÉÂ±Ä
  let formitemlayout: string | any; // formitem ÁöÑÂ∏ÉÂ±Ä

  for (var i = 0; i < formDatas.length; i++) {
    if (formDatas[i].hideInForm) {
      HideFormItemDoM.push(formDatas[i]);
    } else {
      FormItemDoM.push(formDatas[i]);
    }
  }
  // ËÆ°ÁÆó‰∏Ä‰∏™rowÈáåÊéíÂá†‰∏™Ë°®ÂçïÔºõ
  const result = [];
  for (let i = 0, j = FormItemDoM.length; i < j; i++) {
    if (FormItemDoM[i].full) {
      result.push(FormItemDoM.slice(i, i + 1));
    } else {
      if (FormItemDoM[i + 1] && FormItemDoM[i + 1].full) {
        result.push(FormItemDoM.slice(i, i + 1));
      } else if (FormItemDoM[i].defaultcolspan) {
        result.push(FormItemDoM.slice(i, i + FormItemDoM[i].defaultcolspan));
        i = i + FormItemDoM[i].defaultcolspan - 1;
      } else {
        result.push(FormItemDoM.slice(i, i + colspan));
        i = i + colspan - 1;
      }
    }
  }
  // Ê∏≤ÊüìÊàêË°®Âçï;
  const CollapseFormDoM = (item: any, idx: React.Key | null | undefined) => {
    const {
      label,
      name,
      attributes,
      type,
      options,
      onlyimg,
      defaultFormItemLayout,
      full,
      defaultRowColspan,
      hideInForm,
      descItem,
      render,
      // Áî®‰∫éÂà§Êñ≠ÊòØÂê¶ÈúÄË¶ÅËøõË°åÈöêËóèÊòæÁ§∫ (Âú®ÁªÑ‰ª∂Â§ñÂ±ÇÂåÖË£π‰∏ÄÂ±ÇÁªÑ‰ª∂Áî®‰∫éÊéßÂà∂itemÊòæÁ§∫ÂíåÈöêËóè)
      isHide,
      ...otherts
    } = item;
    const dataList = options || [];
    const optionDatas =
      dataList &&
      dataList.length > 0 &&
      dataList.map(
        (
          { value, label, ...others }: any,
          _idx: React.Key | null | undefined,
        ) => {
          if (type === 'select' || type === 'Select') {
            return (
              <Option value={value} key={_idx} {...others}>
                {label}
              </Option>
            );
          } else if (type === 'radio' || type === 'Radio') {
            return (
              <Radio.Button value={value} key={_idx} {...others}>
                {label}
              </Radio.Button>
            );
          }
        },
      );
    const selectOption = optionDatas ? optionDatas : [];
    const rowcolspan_num = [
      colLayout_one,
      colLayout_two,
      colLayout_third,
      colLayout_fourth,
    ];
    const formitemlayout_num = [
      fromItemLayout_conspan_one,
      fromItemLayout_conspan_two,
      fromItemLayout_conspan_third,
      fromItemLayout_conspan_fourth,
    ];
    if (colspan && full) {
      rowcolspan = colLayout_one;
      if (colspan === 3 || colspan === 4) {
        if (props.defaultFormItemLayout) {
          // Â¶ÇÊûúFormCollapseÁªÑ‰ª∂‰∏äÂ∏¶ÊúâdefaulFormItemLayoutÂèÇÊï∞
          formitemlayout = props.defaultFormItemLayout;
          // eslint-disable-next-line max-depth
          if (item.defaultFormItemLayout || item.defaultRowColspan) {
            // Â¶ÇÊûúFormCollapseÁªÑ‰ª∂ÂÜÖÈÉ®ÁöÑÊüê‰∏™Â∞èÁªÑ‰ª∂Â∏¶ÊúâdefaulFormItemLayoutÂèÇÊï∞
            formitemlayout = item.defaultFormItemLayout;
            rowcolspan = item.defaultRowColspan; // ÂçïÁã¨ÁöÑË°®Âçïcol Â∏ÉÂ±Ä
          }
        } else if (item.defaultFormItemLayout || item.defaultRowColspan) {
          //FormCollapseÁªÑ‰ª∂ÂÜÖÈÉ®Âè™ÊúâÊüê‰∏™Â∞èÁªÑ‰ª∂Â∏¶‰∫ÜdefaulFormItemLayoutÂèÇÊï∞
          formitemlayout = item.defaultFormItemLayout;
          rowcolspan = item.defaultRowColspan; // ÂçïÁã¨ÁöÑË°®Âçïcol Â∏ÉÂ±Ä
        } else {
          formitemlayout = fromItemLayout_third_row;
        }
      } else {
        formitemlayout = fromItemLayout_two_row;
      }
    } else {
      rowcolspan = rowcolspan_num[colspan - 1];
      if (props.defaultFormItemLayout) {
        formitemlayout = props.defaultFormItemLayout;
        if (item.defaultFormItemLayout || item.defaultRowColspan) {
          // Â¶ÇÊûúFormCollapseÁªÑ‰ª∂ÂÜÖÈÉ®ÁöÑÊüê‰∏™Â∞èÁªÑ‰ª∂Â∏¶ÊúâdefaultFormItemLayoutÂèÇÊï∞
          formitemlayout = item.defaultFormItemLayout;
          rowcolspan = item.defaultRowColspan; // ÂçïÁã¨ÁöÑË°®Âçïcol Â∏ÉÂ±Ä
        }
      } else if (item.defaultFormItemLayout || item.defaultRowColspan) {
        formitemlayout =
          item.defaultFormItemLayout || formitemlayout_num[colspan - 1];
        rowcolspan = item.defaultRowColspan; // ÂçïÁã¨ÁöÑË°®Âçïcol Â∏ÉÂ±Ä
      } else {
        formitemlayout = formitemlayout_num[colspan - 1];
      }
    }

    // ‰∏ä‰º†ÂõæÁâáÁöÑÊåâÈíÆÂ±ïÁ§∫
    const uploadButtonDom = () => {
      if (item.attributes.listType === 'picture-card') {
        if (item.attributes.imageUrl && item.attributes.imageUrl !== '') {
          return (
            <img
              src={item.attributes.imageUrl}
              alt="avatar"
              style={{ width: '100%' }}
            />
          );
        } else if (item.attributes.fileList) {
          // ‰∏ä‰º†ÁöÑÂõæÁâáÂ§ß‰∫éÊàñÁ≠â‰∫é8Âº†Êó∂ Âπ∂‰∏î Ê≤°ÊúâonlyimgÂèÇÊï∞ÔºåÊòæÁ§∫icon‰∏ä‰º†ÊåâÈíÆ
          if (item.attributes.fileList.length >= 8 && !onlyimg) {
            return (
              <div>
                {item.attributes.loading === 'loading' ? (
                  <LoadingOutlined />
                ) : (
                  <PlusOutlined />
                )}
                <div className="ant-upload-text">‰∏ä‰º†</div>
              </div>
            );
            // ‰∏ä‰º†ÁöÑÂõæÁâáÂ§ß‰∫éÊàñÁ≠â‰∫émaxCountÂº†Êó∂ Âπ∂‰∏î ÊúâonlyimgÂèÇÊï∞Ôºå‰∏çÊòæÁ§∫‰∏ä‰º†ÊåâÈíÆ
          } else if (item.attributes.maxCount && item.attributes.fileList.length >= item.attributes.maxCount && onlyimg) {
            return null;
          }
          return (
            <div>
              {item.attributes.loading === 'loading' ? (
                <LoadingOutlined />
              ) : (
                <PlusOutlined />
              )}
              <div className="ant-upload-text">‰∏ä‰º†</div>
            </div>
          );
        }
      } else {
        return (
          <div>
            <Button>
              <UploadOutlined />
              ‰∏ä‰º†
            </Button>
          </div>
        );
      }
    };
    let renderItem = (
      <Col
        key={idx}
        style={{
          display: item.hideInForm ? 'none' : 'block',
          padding:
            defaultFormLayout && defaultFormLayout === 'vertical'
              ? '0px 12px 8px 12px'
              : '0',
        }}
        className={
          defaultFormLayout && defaultFormLayout === 'vertical'
            ? 'antdp-FormCol'
            : ''
        }
        {...rowcolspan}
      >
        <FormItem
          className="antdp-FormItem"
          colon={false}
          label={label}
          name={name}
          {...(defaultFormLayout && defaultFormLayout === 'vertical'
            ? null
            : formitemlayout)}
          {...otherts}
        >
          {name ? (
            (() => {
              // ÁªÑ‰ª∂Âü∫Á°ÄÂèÇÊï∞
              const componentprams = {
                size: size ? size : 'small',
                ...attributes,
              };
              if (type === 'select' || type === 'Select') {
                return (
                  <Select
                    dropdownMatchSelectWidth={false}
                    allowClear
                    placeholder={
                      attributes && attributes.disabled ? '' : `ËØ∑ÈÄâÊã©${label} `
                    }
                    {...componentprams}
                  >
                    {selectOption}
                  </Select>
                );
              } else if (type === 'radio' || type === 'Radio') {
                return (
                  <Radio.Group size={size ? size : 'small'} {...attributes}>
                    {selectOption}
                  </Radio.Group>
                );
              } else if (type === 'datePicker' || type === 'DatePicker') {
                return (
                  <DatePicker
                    locale={locale}
                    style={{ width: '100%' }}
                    placeholder={
                      attributes && attributes.disabled ? '' : `ËØ∑ÈÄâÊã©${label} `
                    }
                    {...componentprams}
                  />
                );
              } else if (type === 'monthPicker' || type === 'MonthPicker') {
                return (
                  <MonthPicker
                    locale={locale}
                    style={{ width: '100%' }}
                    placeholder={
                      attributes && attributes.disabled ? '' : `ËØ∑ÈÄâÊã©${label} `
                    }
                    {...componentprams}
                  />
                );
              } else if (type === 'rangePicker' || type === 'RangePicker') {
                return (
                  <RangePicker
                    locale={locale}
                    style={{ width: '100%' }}
                    {...componentprams}
                  />
                );
              } else if (
                type === 'timepicker' ||
                type === 'timePicker' ||
                type === 'TimePicker'
              ) {
                return (
                  <TimePicker
                    locale={locale}
                    style={{ width: '100%' }}
                    placeholder={
                      attributes && attributes.disabled ? '' : `ËØ∑ÈÄâÊã©${label} `
                    }
                    {...componentprams}
                  />
                );
              } else if (type === 'cascader' || type === 'Cascader') {
                return (
                  <Cascader
                    placeholder={
                      attributes && attributes.disabled ? '' : `ËØ∑ÈÄâÊã©${label} `
                    }
                    {...componentprams}
                  />
                );
              } else if (type === 'textarea' || type === 'TextArea') {
                return (
                  <Input.TextArea
                    placeholder={
                      attributes && attributes.disabled ? '' : `ËØ∑ËæìÂÖ•${label} `
                    }
                    {...componentprams}
                  />
                );
              } else if (type === 'inputNumber' || type === 'InputNumber') {
                return (
                  <InputNumber
                    placeholder={
                      attributes && attributes.disabled ? '' : `ËØ∑ËæìÂÖ•${label} `
                    }
                    style={{ width: '100%' }}
                    {...componentprams}
                  />
                );
              } else if (type === 'treeSelect' || type === 'TreeSelect') {
                return (
                  <TreeSelect
                    placeholder={
                      attributes && attributes.disabled ? '' : `ËØ∑ÈÄâÊã©${label} `
                    }
                    {...componentprams}
                  />
                );
              } else if (type === 'checkbox' || type === 'Checkbox') {
                if (
                  (item.options && item.options.length > 0) ||
                  (item.option && item.option.length > 0)
                ) {
                  return (
                    <Checkbox.Group
                      options={item.options || item.option}
                      {...attributes}
                    />
                  );
                }
                return (
                  <Checkbox {...attributes}>
                    {label || item.checkboxLable}
                  </Checkbox>
                );
              } else if (type === 'UploadGrid' || type === 'uploadGrid') {
                return (
                  <UploadGrid {...attributes}>{uploadButtonDom()}</UploadGrid>
                );
              } else if (type === 'autoComplete' || type === 'AutoComplete') {
                return (
                  <AutoComplete
                    placeholder={
                      attributes && attributes.disabled ? '' : `ËØ∑ËæìÂÖ•${label} `
                    }
                    {...componentprams}
                  />
                );
              } else if (type === 'Password') {
                return (
                  <Input.Password
                    placeholder={
                      attributes && attributes.disabled ? '' : `ËØ∑ËæìÂÖ•${label} `
                    }
                    {...componentprams}
                  />
                );
              } else if (type === 'inputCount' || type === 'InputCount') {
                return (
                  <InputCount
                    placeholder={
                      attributes && attributes.disabled ? '' : `ËØ∑ËæìÂÖ•${label} `
                    }
                    {...attributes}
                  />
                );
              } else if (type === 'render') {
                return render && render
              } else {
                if (
                  (attributes && attributes.type === 'Search') ||
                  type === 'InputSearch'
                ) {
                  const suffix = (
                    <AudioOutlined
                      style={{
                        fontSize: 16,
                        color: '#fff',
                      }}
                    />
                  );
                  return (
                    <Search
                      suffix={suffix}
                      placeholder={
                        attributes && attributes.disabled
                          ? ''
                          : `ËØ∑ËæìÂÖ•${label} `
                      }
                      {...componentprams}
                    />
                  );
                }
                return (
                  <Input
                    placeholder={
                      attributes && attributes.disabled ? '' : `ËØ∑ËæìÂÖ•${label} `
                    }
                    {...componentprams}
                  />
                );
              }
            })()
          ) : (
            <Input
              placeholder={
                attributes && attributes.disabled ? '' : `ËØ∑ËæìÂÖ•${label} `
              }
              size={size}
              {...attributes}
            />
          )}
        </FormItem>
      </Col>
    )

    if (isHide && name) {
      return (
        <Hide key={idx} name={name}>
          {renderItem}
        </Hide>
      );
    }


    return renderItem;
  };
  // ÈöêËóèÁöÑË°®ÂçïÈõÜÂêà
  const hideCollapseForm = HideFormItemDoM.map((item, idx) =>
    CollapseFormDoM(item, idx),
  );
  // Ë°®ÂçïÈõÜÂêà
  const CollapseForm = result.map((it, indix) => {
    return (
      <Row key={indix}>
        {it.map((item, idx) => {
          return CollapseFormDoM(item, idx);
        })}
      </Row>
    );
  });
  // Form+Ë°®ÂçïÈõÜÂêà
  const FormDom = (
    <HideContext.Provider value={hide} >
      <ConfigProvider locale={zhCN}>
        <Form
          layout={defaultFormLayout ? defaultFormLayout : 'horizontal'}
          ref={ref}
          {...(defaultFormLayout && defaultFormLayout === 'vertical'
            ? null
            : formitemlayout)}
          {...otherProps}
        >
          <Row>{hideCollapseForm}</Row>
          <div>{CollapseForm}</div>
        </Form>
      </ConfigProvider>
    </HideContext.Provider>
  );
  // type ‰∏∫ modalÊó∂Ê≤°ÊúâÊäòÂè†ÔºåÊ≤°ÊúâÊ†áÈ¢òÔºåÁõ¥Êé•ÊòæÁ§∫formË°®ÂçïÂÜÖÂÆπ
  if (type === 'modal') {
    return <div style={{ margin: -10 }}>{FormDom}</div>
  }
  // type ‰∏∫CardPro  Â∏¶Ê†áÈ¢ò
  if (type === 'CardPro') {
    return (
      <CardPro title={header}>
        <div className="antdp-FormBox">{FormDom}</div>
      </CardPro>
    );
  }
  // type ‰∏∫cardform Êó∂ ÊòæÁ§∫Ë°®ÂçïÔºåÂàÜÂâ≤Á∫ø ÂàÜÁ¶ªÊØè‰∏™Ë°®Âçï
  if (type === 'cardform') {
    return (
      <div>
        <h3 className="antdp-FormTitle">{header}</h3>
        {FormDom}
        <Divider type="horizontal" className="antdp-FormDivider" />
      </div>
    );
  }
  return (
    <Collapse
      defaultActiveKey={!visible ? ['1'] : ''}
      {...collapseAttributes}
      className="antdp-mb10"
    >
      <Panel header={header} key="1" {...panelAttributes} extra={extra}>
        {FormDom}
      </Panel>
    </Collapse>
  );
};

const FormForward = forwardRef(QuickForm)

type initFormProps = typeof FormForward

interface QFormProps extends initFormProps {
  useHideContext: typeof useHideContext;
  useFormItemHide: typeof useFormItemHide
}

const QForm = FormForward as QFormProps

QForm.useHideContext = useHideContext

QForm.useFormItemHide = useFormItemHide

export default QForm
